.ConteneurGlobal{
    margin: 20px 10px;
    padding: 0;
    background-color: #cdcdcd;
    a, a:active, a:visited, a:link{
        color: inherit;
        text-decoration: none;
    }
    a.urlActive{
        font-weight: bold;
        color: chartreuse;
    }
    .contenuPrincipal{
        margin: 40px;
        h2{   
            margin: 0 0 20px 0;
            padding: 0 0 10px 0;
            border-bottom: 2px solid #666;
        }
    }
}
